import errMissingPicture from 'assets/static/no-photos.png';
import spinner from 'assets/static/spinner.gif';
import { ImgHTMLAttributes, useEffect, useState } from 'react';
import { RequiredProps } from 'types/util';
import { log } from 'utils';
import styles from './style.module.css';

type Props = RequiredProps<ImgHTMLAttributes<HTMLImageElement>, 'src'>;

const Img = ({ src, alt, className, ...rest }: Props) => {
  const [imgSrc, setImgSrc] = useState(spinner);
  const [err, setErr] = useState(false);

  useEffect(() => {
    setImgSrc(spinner);
    setErr(false);

    if (!src) return;

    try {
      const img = new Image();
      img.src = require(`assets/${src}`);
      img.onload = () => {
        setImgSrc(img.src);
      };
    } catch (e) {
      log({
        error: e,
        src,
        asset: `assets/${src}`,
      });
      setImgSrc(errMissingPicture);
      setErr(true);
    }
  }, [src]);

  const imgLoaded = imgSrc !== spinner && imgSrc !== errMissingPicture;

  return imgLoaded ? (
    <img src={imgSrc} alt={alt} className={className} {...rest} />
  ) : (
    <img
      src={imgSrc}
      alt={alt}
      title={`${alt ? `${alt} | ` : ''}${
        err ? 'Sorry, this picture is currently unavailable.' : 'Loading...'
      }`}
      className={styles.missing}
    />
  );
};

export default Img;
