import AlwaysScrollToBottom from 'components/AlwaysScrollToBottom';
import Messages from 'components/Messages';
import Post from 'components/Post';
import Profile from 'components/Profile';
import Response from 'components/Response';
import ResponsePrompt from 'components/ResponsePrompt';
import { useModuleBodyStylesEffect, useModuleStyles } from 'hooks/module-styles';
import { Dispatch, useEffect } from 'react';
import {
  ChatMessages,
  ChatProfile,
  GameResponseOption,
  GameStepConfig,
  GetChatProfileFn,
  Persona
} from 'types/configs';
import { Action } from 'types/reducer';
import { State } from 'types/state';
import { log, shuffle } from 'utils';
import styles from './style.module.css';

const personas: Record<Persona, ChatProfile> = {
  Robin: {
    id: 'Robin',
    name: 'Robin (Cyber Security Expert)',
    avatar: 'Robin',
  },
  Kuma: {
    id: 'Kuma',
    name: 'Kuma (Bot)',
    avatar: 'Kuma',
  },
  Sightshare: {
    id: 'Sightshare',
    name: 'Sightshare (System message)',
    avatar: 'Sightshare',
  },
  expert: {
    id: 'expert',
    name: 'Robin (Cyber Security Expert)',
    avatar: 'expert',
  },
  // friend1: {
  //   id: 'friend1',
  //   name: '',
  //   avatar: 'expert.png',
  // },
  // friend2: {
  //   id: 'friend2',
  //   name: '',
  //   avatar: 'expert.png',
  // },
};

const mergeMessages = (
  stateMessages: ChatMessages[],
  stepMessages: ChatMessages[],
): ChatMessages[] => {
  if (stateMessages.length > 0 && stepMessages.length > 0) {
    const lastStateMsg = stateMessages.slice(-1)[0];
    const firstStepMsg = stepMessages.slice(0, 1)[0];

    if (
      lastStateMsg.type === 'message' &&
      firstStepMsg.type === 'message' &&
      lastStateMsg.from === firstStepMsg.from
    ) {
      return [
        ...stateMessages.slice(0, -1),
        {
          type: lastStateMsg.type,
          from: lastStateMsg.from,
          messages: [...lastStateMsg.messages, ...firstStepMsg.messages],
        },
        ...stepMessages.slice(1),
      ];
    }
  }

  return [...stateMessages, ...stepMessages];
};

interface Props {
  config: GameStepConfig;
  state: State;
  dispatch: Dispatch<Action>;
}

const Game = ({ config, state, dispatch }: Props) => {
  const moduleClasses = useModuleStyles(styles);
  useModuleBodyStylesEffect(styles);

  const { messages: stepMessages, response: configResponse = { options: [{ id: 'ok', label: 'OK' }] }, id, loadHandler, shouldLoad } = config;
  const { config: stateConfig, step, profile, post, messages: stateMessages } = state;


  const response = {
    ...configResponse,
    ...( stateConfig[id]?.responseOptions ? { options: stateConfig[id]?.responseOptions } : {}),
  };

  const { randomise, options } = configResponse;
  const shouldSetResponseOptions = stateConfig[id]?.responseOptions === undefined;
  const shouldRandomise = randomise === undefined || randomise;

   useEffect(() => {
    if (!shouldSetResponseOptions) return;

    dispatch({
      type: 'SET_RESPONSE_OPTIONS', 
      id, 
      responseOptions: shouldRandomise ? shuffle(options) : options
    })
  }, [id, shouldSetResponseOptions, shouldRandomise, options, dispatch]);


  const shouldHandleLoad = loadHandler && shouldLoad && shouldLoad(state);
  useEffect(() => {
    if (shouldHandleLoad) {
      dispatch({ type: 'CUSTOM_HANDLER', handler: loadHandler });
    }
  }, [shouldHandleLoad, dispatch, loadHandler]);


  const messages = mergeMessages(stateMessages, stepMessages);

  const handleResponse = (respOpt: GameResponseOption | GameResponseOption[]) => {
    const responseOption = Array.isArray(respOpt) ? respOpt : [respOpt];

    log({
      responseOption,
      response,
    });

    dispatch({
      type: 'RESPONSE',
      response: {
        id,
        invalid: false,
        response: responseOption.map((resp) => resp.id),
      },
    });

    if (config.responseHandler) {
      dispatch({ type: 'CUSTOM_HANDLER', handler: config.responseHandler(responseOption) });
    }

    dispatch({
      type: 'SET_MESSAGES',
      messages: [
        ...messages,
        {
          type: 'response',
          messages: responseOption.map(({ label }) => label),
        },
        ...mergeMessages(
          responseOption[0].answer ? [responseOption[0].answer] : [],
          response?.followUp ? [response?.followUp] : [],
        ),
      ],
    });

    dispatch({ type: 'STEP_SET_VALID', step, valid: true });

    if (responseOption[0].nextStepId) {
      dispatch({ type: 'GOTO_STEP_ID', id: responseOption[0].nextStepId });
    } else {
      dispatch({ type: 'NEXT' });
    }
  };

  const getChatProfile =
    (state: State): GetChatProfileFn =>
    (persona: Persona): ChatProfile => {
      return {
        ...personas[persona],
        name: personas[persona].name,
        // name: persona === 'friend1' || persona === 'friend2' ? state[persona] : personas[persona].name,
      };
    };

  const enrichMessage =
    (state: State) =>
    (message: string): string =>
      message.replace(/\$\{([^}]+)\}/g, (match, key = '') => {
        return state.variables[key] ? state.variables[key].toString() : '';
      });

  return (
    <div className={moduleClasses('container')}>
      <Profile {...profile} moduleClasses={moduleClasses} />
      <Post {...post} moduleClasses={moduleClasses} />
      <div className={moduleClasses('item content')}>
        <div className={moduleClasses('content-container')}>
          <>
            {messages.map((message, i) =>
              message.type === 'message' ? (
                <Messages
                  {...message}
                  key={i}
                  enrichMessage={enrichMessage(state)}
                  getChatProfile={getChatProfile(state)}
                  moduleClasses={moduleClasses}
                />
              ) : (
                <Response
                  key={i}
                  from={profile}
                  messages={message.messages}
                  moduleClasses={moduleClasses}
                />
              ),
            )}
          </>
          <ResponsePrompt
            from={profile}
            response={response}
            handleResponse={handleResponse}
            moduleClasses={moduleClasses}
          />
          <AlwaysScrollToBottom behavior="smooth" />
        </div>
      </div>
    </div>
  );
};

export default Game;
