import Avatar from 'components/Avatar';
import ResponseButton from 'components/ResponseButton';
import { useModuleStyles } from 'hooks/module-styles';
import { ChangeEvent, useEffect, useState } from 'react';
import { GameResponse, GameResponseOption, Profile } from 'types/configs';
import styles from './style.module.css';

interface Props {
  from: Profile;
  response: GameResponse;
  handleResponse: (respOpt: GameResponseOption[]) => void;
  moduleClasses: (classes: string) => string;
}

const ResponsePromptMultipleChoice = ({
  from: { avatar },
  response: { prompt, options },
  handleResponse,
  moduleClasses,
}: Props) => {
  const componentClasses = useModuleStyles(styles);

  const [responses, setResponses] = useState(options);
  const [selected, setSelected] = useState([] as GameResponseOption['id'][]);

  useEffect(() => {
    // Reset selection for new options:
    setSelected([]);
    setResponses(options);
  }, [options]);

  const handleOnChange = (e: ChangeEvent<HTMLInputElement>) => {
    const { value, checked } = e.target;
    setSelected([...selected.filter((v) => v !== value), ...(checked ? [value] : [])]);
  };

  const handleConfirm = () => {
    if (selected.length === 0) return;
    handleResponse(responses.filter((resp) => selected.includes(resp.id)));
  };

  return (
    <div className={moduleClasses('content-item responses')}>
      <div className={moduleClasses('res-container')}>
        <div className={moduleClasses('res-item res-prompt')}>
          {prompt ? prompt : 'Select all that apply:'}
        </div>
        <div className={moduleClasses('res-item res-photo')}>
          <Avatar avatar={avatar} />
        </div>
        {responses.map((response) => (
          <div
            key={response.id}
            className={`
                  ${componentClasses(
                    `survey-response ${selected.includes(response.id) && 'selected'}`,
                  )} 
                  ${moduleClasses('res-item response')}
                `}
          >
            <label>
              <input
                type="checkbox"
                value={response.id}
                id={response.id}
                checked={selected.includes(response.id)}
                onChange={handleOnChange}
              />
              {response.label}
            </label>
          </div>
        ))}

        <div
          className={`
            ${moduleClasses('res-item response')} 
            ${componentClasses('confirm-button')}
          `}
        >
          <ResponseButton
            disabled={selected.length === 0}
            id="response-send"
            onClick={handleConfirm}
          >
            Confirm
          </ResponseButton>
        </div>
      </div>
    </div>
  );
};

export default ResponsePromptMultipleChoice;
