import Avatar from 'components/Avatar';
import { ChatResponse, Profile } from 'types/configs';

interface Props {
  from: Profile;
  messages: ChatResponse['messages'];
  moduleClasses: (classes: string) => string;
}

const Response = ({ from, messages, moduleClasses }: Props) => {
  return (
    <div className={moduleClasses('content-item responses')}>
      <div className={moduleClasses('res-container')}>
        <div className={moduleClasses('msg-item res-name')}>{from.name}</div>
        <div className={moduleClasses('res-item res-photo')}>
          <Avatar avatar={from.avatar} />
        </div>
        {messages.map((message, i) => (
          <div key={i} className={moduleClasses('res-item res-msg')}>
            {message}
          </div>
        ))}
      </div>
    </div>
  );
};

export default Response;
