import { getResponse, State } from 'types/state';
import { log } from 'utils';
import {
  GameContentConfig,
  GameHandlersConfig,
  GameStepHandlersConfig,
  SurveyConfig,
  SurveyConfigOld,
} from 'types/configs';
import {
  FOLLOWERS_COEF,
  FRIENDS_COEF_CORRECT,
  FRIENDS_COEF_INCORRECT,
  LIKES_FOLLOWERS_COEF,
  LIKES_FRIENDS_COEF,
} from './game/handlers/coeficients';

export const getNewPostHandlers = (
  postId: string,
  postImgSrc: string | ((state: State) => string),
  postImgAlt?: string,
): Required<Pick<GameStepHandlersConfig, 'shouldLoad' | 'loadHandler'>> => ({
  shouldLoad: (state) => state.post.id !== postId,
  loadHandler: (state) => {
    const friends = Math.max(parseInt(getResponse(state, 'friends', '100'), 10), 10);

    return {
      ...state,
      post: {
        ...state.post,
        id: postId,
        img: {
          src: typeof postImgSrc === 'function' ? postImgSrc(state) : postImgSrc,
          alt: postImgAlt,
        },

        likes: state.post.likes || Math.round(friends * 0.4),
        shares: state.post.shares || Math.max(Math.round(friends * 0.08), 1),
        views: state.post.views || Math.round(friends * 1.2),
      },
      messages: [],

      // Initialise if needed:
      profile: state.profile.loaded
        ? state.profile
        : {
            name: getResponse(state, 'name', 'Jane Doe'),
            avatar: getResponse(state, 'avatar'),
            friends,
            followers: Math.round(friends * 0.1),
            loaded: true,
          },
    };
  },
});

export const getUpdateFriendsFn =
  (
    correctResponseId?: string,
    correctCoeficient: number = FRIENDS_COEF_CORRECT,
    incorrectCoeficient: number = FRIENDS_COEF_INCORRECT,
  ): UpdateFriendsFn =>
  (currentFriends: number, responseId: string) =>
    correctResponseId === undefined || responseId === correctResponseId
      ? Math.round(currentFriends * correctCoeficient)
      : Math.round(currentFriends * incorrectCoeficient);

export const getUpdateFollowersFn =
  (followersCoeficient: number = FOLLOWERS_COEF): UpdateFollowersFn =>
  (currentFollowers: number) =>
    Math.round(currentFollowers * followersCoeficient);

export const getUpdateLikesFn =
  (
    friendsCoeficient: number = LIKES_FRIENDS_COEF,
    followersCoeficient: number = LIKES_FOLLOWERS_COEF,
  ): UpdateLikesFn =>
  (currentLikes: number, currentFriends: number, currentFollowers: number) =>
    Math.round(friendsCoeficient * currentFriends + followersCoeficient * currentFollowers);

// export type UpdateFn = <T>(currentValue: T, state: State, response: GameResponseOption) => T;
// export type GetUpdateFn = () => UpdateFn;

export const keepSameFn =
  () =>
  <T>(currentValue: T): T =>
    currentValue;

export type UpdateFriendsFn = (currentValue: number, responseId: string) => number;
export type UpdateFollowersFn = (currentValue: number) => number;
export type UpdateLikesFn = (
  currentValue: number,
  currentFriends: number,
  currentFollowers: number,
) => number;

export const getUpdateHandler = (
  updateFriendsFn: UpdateFriendsFn,
  updateFollowersFn: UpdateFollowersFn,
  updateLikesFn: UpdateLikesFn,
): Required<Pick<GameStepHandlersConfig, 'responseHandler'>> => ({
  responseHandler:
    ([{ id: responseId }]) =>
    (state) =>
      updateStateStats(
        state,
        updateFriendsFn(state.profile.friends, responseId),
        updateFollowersFn(state.profile.followers),
        updateLikesFn(state.post.likes, state.profile.friends, state.profile.followers),
      ),
});

export interface DefaultUpdateHandlerParams {
  correctResponseId?: string;
  friendsCorrectCoeficient?: number;
  friendsIncorrectCoeficient?: number;
  followersCoeficient?: number;
  likesFriendsCoeficient?: number;
  likesFollowersCoeficient?: number;
}

export const defaultUpdateHandlerParams: DefaultUpdateHandlerParams = {
  friendsCorrectCoeficient: FRIENDS_COEF_CORRECT,
  friendsIncorrectCoeficient: FRIENDS_COEF_INCORRECT,
  followersCoeficient: FOLLOWERS_COEF,
  likesFriendsCoeficient: LIKES_FRIENDS_COEF,
  likesFollowersCoeficient: LIKES_FOLLOWERS_COEF,
};

export const getDefaultUpdateHandler = (parameters: DefaultUpdateHandlerParams = {}) => {
  const {
    correctResponseId,
    friendsCorrectCoeficient,
    friendsIncorrectCoeficient,
    followersCoeficient,
    likesFriendsCoeficient,
    likesFollowersCoeficient,
  } = {
    ...defaultUpdateHandlerParams,
    ...parameters,
  };

  return getUpdateHandler(
    getUpdateFriendsFn(correctResponseId, friendsCorrectCoeficient, friendsIncorrectCoeficient),
    getUpdateFollowersFn(followersCoeficient),
    getUpdateLikesFn(likesFriendsCoeficient, likesFollowersCoeficient),
  );
};

export type MultiUpdateHandlers = Record<string, ReturnType<typeof getUpdateHandler>>;

export const getMultiUpdateHandlers = (ids: string[], parameters = {}) =>
  ids.reduce(
    (acc, id) => ({
      ...acc,
      [id]: getDefaultUpdateHandler(parameters),
    }),
    {} as MultiUpdateHandlers,
  );

export const updateStateStats = (
  state: State,
  friends: number,
  followers: number,
  likes: number,
): State => {
  const lostFriends = state.profile.friends - friends;
  log({
    prevFriends: state.profile.friends,
    prevFollowers: state.profile.followers,
    prevLikes: state.post.likes,
    friends,
    followers,
    likes,
    lostFriends,
  });

  return {
    ...state,
    profile: {
      ...state.profile,
      friends,
      followers,
    },
    post: {
      ...state.post,
      likes,
    },
    variables: {
      ...state.variables,
      lostFriends,
    },
  };
};

export const convertOldConfig = (oldConfig: SurveyConfigOld): SurveyConfig =>
  oldConfig.reduce(
    (cnf, stepCnf) => ({
      ...cnf,
      [stepCnf.id]: {
        ...(cnf[stepCnf.id] ? cnf[stepCnf.id] : {}),
        ...stepCnf,
      },
    }),
    {} as SurveyConfig,
  );

export const generateConfig = (
  contentCnf: GameContentConfig,
  handlersCnf: GameHandlersConfig,
): SurveyConfig =>
  Object.entries(contentCnf).reduce(
    (config, [key, content]) => ({
      ...config,
      [key]: {
        type: 'game',
        ...content,
        ...(handlersCnf[key] ? handlersCnf[key] : {}),
      },
    }),
    {} as SurveyConfig,
  );
