import {
  ChatMessage,
  GameResponse,
  GameResponseOption,
  GameStepContentConfig,
  Persona,
  isChatMessage,
  isGameResponse,
  personas,
} from 'types/configs';

export const configJsonItemKeys = [
  'ID',
  'From',
  'Message',
  'Image',
  'Answer ID',
  'Answer',
  'Next step',
  'Follow up',
  'Randomise',
  'Multi-Choice',
  'Correct Ans ID',
  'Fr Coef - true',
  'Fr Coef - false',
  'Followers',
  'Likes',
] as const;

export type ConfigJsonItemKeys = (typeof configJsonItemKeys)[number];
export type ConfigJsonItem = Record<ConfigJsonItemKeys, string>;
export type ConfigJson = ConfigJsonItem[];

export interface ConfigJsonValidationError {
  error: string;
  missingKeys?: string[];
  extraKeys?: string[];
  invalidPersona?: string;
  context?: any;
}

export class ConfigJsonValidationErrors extends Error {
  errors: ConfigJsonValidationError[];
  constructor(errors: ConfigJsonValidationError | ConfigJsonValidationError[]) {
    super('Config Validation Error');
    this.name = 'ConfigJsonValidationErrors';
    this.errors = Array.isArray(errors) ? errors : [errors];
  }
}

export const isConfigJson = (json: unknown): json is ConfigJson => {
  if (!Array.isArray(json)) throw new ConfigJsonValidationErrors({ error: 'not an array' });

  const validateRow = (row: number, item: unknown): ConfigJsonValidationError[] => {
    if (typeof item !== 'object') return [{ error: 'item not an object', context: { row, item } }];
    if (item === null) return [{ error: 'item is null', context: { row, item } }];

    const itemKeys = Object.keys(item);
    const missingKeys = configJsonItemKeys.filter((key) => !itemKeys.includes(key));
    const extraKeys = itemKeys.filter(
      (key) => !configJsonItemKeys.includes(key as ConfigJsonItemKeys),
    );
    const from = (item as ConfigJsonItem).From as Persona | '';
    const invalidPersona = from !== '' && !personas.includes(from);

    // console.log({
    //   row,
    //   item,
    //   itemKeys,
    //   configJsonItemKeys,
    //   missingKeys,
    //   extraKeys,
    // });

    if (missingKeys.length > 0 || extraKeys.length > 0 || invalidPersona) {
      return [
        {
          error: 'invalid object',
          ...(missingKeys.length > 0 ? { missingKeys } : {}),
          ...(extraKeys.length > 0 ? { extraKeys } : {}),
          invalidPersona: from,
          context: { row, item },
        },
      ];
    }

    return [];
  };

  const validationErrors: ConfigJsonValidationError[] = json.reduce(
    (acc: ConfigJsonValidationError[], item: unknown, row) => {
      return [...acc, ...validateRow(row, item)];
    },
    [],
  );

  if (validationErrors.length > 0) throw new ConfigJsonValidationErrors(validationErrors);

  return true;
};

export interface FlatGameResponse extends Omit<GameResponse, 'options'> {
  options: {
    [answerId: string]: GameResponseOption;
  };
}

export interface FlatStep {
  id: string;
  messages: {
    [from: string]: ChatMessage;
  };
  response?: FlatGameResponse;
}

export interface FlatConfig {
  [id: string]: FlatStep;
}

export const isGameStepContentConfig = (config: unknown): config is GameStepContentConfig => {
  if (typeof config !== 'object') throw new ConfigJsonValidationErrors({ error: 'not an object' });
  if (config === null) throw new ConfigJsonValidationErrors({ error: 'is null' });

  const { id, messages, response } = config as GameStepContentConfig;

  const invalidId = typeof id !== 'string' || id === '' ? [{ error: 'invalid id' }] : [];

  const invalidMsgs = !Array.isArray(messages)
    ? [{ error: 'messages not an array' }]
    : messages.reduce(
        (acc, msg, row) =>
          !isChatMessage(msg) ? [...acc, { row, error: 'invalid message', context: msg }] : acc,
        [] as ConfigJsonValidationError[],
      );

  const invalidResp =
    response !== undefined && !isGameResponse(response)
      ? [{ error: 'invalid response', context: response }]
      : [];

  const errors: ConfigJsonValidationError[] = [...invalidId, ...invalidMsgs, ...invalidResp];

  if (errors.length > 0) throw new ConfigJsonValidationErrors(errors);

  return true;
};

export const isGameConfigOld = (config: unknown): config is GameStepContentConfig[] => {
  if (!Array.isArray(config)) throw new ConfigJsonValidationErrors({ error: 'not an array' });
  return config.every((stepConfig) => isGameStepContentConfig(stepConfig));
};
