import { getResponse } from 'types/state';
import { log } from 'utils';
import { GameHandlersConfig } from 'types/configs';
import {
  getNewPostHandlers,
  getUpdateFollowersFn,
  getUpdateLikesFn,
  updateStateStats,
} from '../../helpers';

export const gameQuickTasksHandlers: GameHandlersConfig = {
  'QT1.1': {
    ...getNewPostHandlers('QT1.1', 'quick-tasks/quick-task-1.png'),
  },

  'QT2.1': {
    ...getNewPostHandlers('QT2.1', 'quick-tasks/quick-task-2.png'),
  },

  'QT3.1': {
    ...getNewPostHandlers('QT3.1', 'quick-tasks/quick-task-3.png'),
  },

  'QT3.2': {
    responseHandler: () => (state) => {
      const correctAnswers: Record<string, string> = {
        'QT1.1': 'QT1.1.3',
        'QT2.1': 'QT2.1.1',
        'QT3.1': 'QT3.1.3',
      };

      const friendsCoeficients: Record<number, number> = {
        3: 1,
        2: 0.8,
        1: 0.7,
        0: 0.6,
      };

      const correctResponses = Object.entries(correctAnswers).reduce(
        (acc, [key, value]) => (getResponse(state, key) === value ? acc + 1 : acc),
        0,
      );

      log({
        correctResponses,
        friendsCoeficient: friendsCoeficients[correctResponses],
      });

      return updateStateStats(
        state,
        Math.round(state.profile.friends * friendsCoeficients[correctResponses]),
        getUpdateFollowersFn(1.2)(state.profile.followers),
        getUpdateLikesFn(0.4, 0.6)(
          state.post.likes,
          state.profile.friends,
          state.profile.followers,
        ),
      );
    },
  },

  'QT4.1': {
    ...getNewPostHandlers('QT4.1', 'quick-tasks/quick-task-4.png'),
  },

  'QT5.1': {
    ...getNewPostHandlers('QT5.1', 'quick-tasks/quick-task-5.png'),
  },

  'QT6.1': {
    ...getNewPostHandlers('QT6.1', 'quick-tasks/quick-task-6.png'),
  },

  'QT6.2': {
    responseHandler: () => (state) => {
      const correctAnswers: Record<string, string> = {
        'QT4.1': 'QT4.1.3',
        'QT5.1': 'QT5.1.1',
        'QT6.1': 'QT6.1.3',
      };

      const friendsCoeficients: Record<number, number> = {
        3: 1,
        2: 0.8,
        1: 0.6,
        0: 0.4,
      };

      const correctResponses = Object.entries(correctAnswers).reduce(
        (acc, [key, value]) => (getResponse(state, key) === value ? acc + 1 : acc),
        0,
      );

      log({
        correctResponses,
        friendsCoeficient: friendsCoeficients[correctResponses],
      });

      return updateStateStats(
        state,
        Math.round(state.profile.friends * friendsCoeficients[correctResponses]),
        getUpdateFollowersFn(1.2)(state.profile.followers),
        getUpdateLikesFn(0.4, 0.6)(
          state.post.likes,
          state.profile.friends,
          state.profile.followers,
        ),
      );
    },
  },
};
