import { ResponseRangeOption, SurveyStepConfigRange } from 'types/configs';
import { useEffect } from 'react';
import { State } from 'types/state';
// import { log } from 'utils';

interface Props {
  config: SurveyStepConfigRange;
  state: State;
  selected?: string;
  setResponse: (response: string, invalid?: boolean) => void;
  next?: () => void;
  moduleClasses: (classes: string) => string;
}

const SurveyRange = ({ config, state, selected, setResponse, next, moduleClasses }: Props) => {
  const { rangeLabel, optionsLabels } = config;
  const min = config.min !== undefined ? config.min : 1;
  const max = config.max !== undefined ? config.max : 5;
  const length = max - min + 1;

  const options: ResponseRangeOption[] = Array(length)
    .fill(undefined)
    .map((_, i) => ({
      id: (i + min).toString(),
      label: (i + min).toString(),
      desc: optionsLabels?.[i],
    }));

  // log({
  //   min,
  //   max,
  //   length,
  //   options,
  // });

  useEffect(() => {
    const handleKeyboardInput = (e: KeyboardEvent) => {
      const keys = options.map((opt) => opt.id);

      if (keys.includes(e.key)) {
        setResponse(e.key);
      }

      if (e.key === 'Enter' && selected) {
        next?.();
      }
    };

    document.addEventListener('keydown', handleKeyboardInput, false);
    return () => {
      document.removeEventListener('keydown', handleKeyboardInput, false);
    };
  }, [next, options, selected, setResponse]);

  return (
    <div className={moduleClasses('survey-range')}>
      {rangeLabel && (
        <div className={moduleClasses('survey-range-label')}>
          {typeof rangeLabel === 'function' ? rangeLabel(state) : rangeLabel}
        </div>
      )}

      <div className={moduleClasses(`survey-range-response-desc-container`)}>
        {options.map(({ desc, id }) => (
          <div key={id} className={moduleClasses(`survey-range-response-desc`)}>
            {desc}
          </div>
        ))}
      </div>

      <div className={moduleClasses('survey-range-options')}>
        {options.map(({ id, label }) => (
          <div key={id} className={moduleClasses(`survey-range-response-container`)}>
            <button
              className={moduleClasses(`survey-range-response ${selected === id && 'selected'}`)}
              onClick={() => {
                setResponse(id);
                next?.();
              }}
            >
              {label}
            </button>
            {/* <div className={moduleClasses(`survey-range-response-desc`)}>{desc}</div> */}
          </div>
        ))}
      </div>
    </div>
  );
};

export default SurveyRange;
