import Avatar from 'components/Avatar';
import ResponseButton from 'components/ResponseButton';
import { GameResponse, GameResponseOption, Profile } from 'types/configs';

interface Props {
  from: Profile;
  response: GameResponse;
  handleResponse: (response: GameResponseOption) => void;
  moduleClasses: (classes: string) => string;
}

const ResponsePromptSingleChoice = ({
  from: { avatar },
  response: { prompt, options },
  handleResponse,
  moduleClasses,
}: Props) => (
    <div className={moduleClasses('content-item responses')}>
      <div className={moduleClasses('res-container')}>
        <div className={moduleClasses('res-item res-prompt')}>
          {prompt ? prompt : 'Choose one of the following options to respond:'}
        </div>
        <div className={moduleClasses('res-item res-photo')}>
          <Avatar avatar={avatar} />
        </div>
        {options.map((response) => (
          <div key={response.id} className={moduleClasses('res-item response')}>
            <ResponseButton id={response.id} onClick={() => handleResponse(response)}>
              {response.label}
            </ResponseButton>
          </div>
        ))}
      </div>
    </div>
  );

export default ResponsePromptSingleChoice;
