import Avatar from 'components/Avatar';
import { Profile as ProfileType } from 'types/configs';

interface Props extends ProfileType {
  moduleClasses: (classes: string) => string;
}

const Profile = ({ name, avatar, friends, followers, moduleClasses }: Props) => (
  <>
    <div className={moduleClasses('item photo')}>
      <Avatar avatar={avatar} />
    </div>
    <div className={moduleClasses('item account')}>
      <p title={name}>{name}</p>
    </div>
    <div className={moduleClasses('item friends')}>
      <span>
        <b>{friends}</b> friends
      </span>
    </div>
    <div className={moduleClasses('item followers')}>
      <span>
        <b>{followers}</b> followers
      </span>
    </div>
  </>
);

export default Profile;
