import errMissingPicture from 'assets/static/no-photos.png';
import spinner from 'assets/static/spinner.gif';
import { ImgHTMLAttributes, useCallback, useEffect, useState } from 'react';
import { RequiredProps } from 'types/util';
import { log } from 'utils';
import styles from './style.module.css';

type Props = RequiredProps<ImgHTMLAttributes<HTMLImageElement>, 'src'> & {
  zoomable?: boolean;
};

const ImageZoomable = ({ src, alt, className, zoomable = true, ...rest }: Props) => {
  const [zoomed, setZoomed] = useState(false);
  const [imgSrc, setImgSrc] = useState(spinner);

  const handleEsc = useCallback((e: KeyboardEvent) => {
    if (e.key === 'Escape') {
      setZoomed(false);
    }
  }, []);

  useEffect(() => {
    document.addEventListener('keydown', handleEsc, false);
    return () => {
      document.removeEventListener('keydown', handleEsc, false);
    };
  }, [handleEsc]);

  useEffect(() => {
    setImgSrc(spinner);

    if (!src) return;

    try {
      const img = new Image();
      img.src = require(`assets/${src}`);
      img.onload = () => {
        setImgSrc(img.src);
      };
    } catch (e) {
      log({
        error: e,
        src,
        asset: `assets/${src}`,
      });
      setImgSrc(errMissingPicture);
    }
  }, [src]);

  const imgLoaded = imgSrc !== spinner && imgSrc !== errMissingPicture;

  return imgLoaded ? (
    <>
      <div className={styles.zoom} onClick={() => setZoomed(true)}>
        <img src={imgSrc} alt={alt} className={className} {...rest} />
        <div className={styles.overlay}></div>
      </div>

      {zoomed && (
        <picture className={styles.lightbox} onClick={() => setZoomed(false)}>
          <img src={imgSrc} alt={alt} className={styles['lightbox-img']} {...rest} />
        </picture>
      )}
    </>
  ) : (
    <img
      src={imgSrc}
      alt={alt}
      title={`${alt ? `${alt} | ` : ''}This picture is currently loading or unavailable.`}
      className={styles.missing}
    />
  );
};

export default ImageZoomable;
