import ResponsePromptMultipleChoice from 'components/ResponsePromptMultipleChoice';
import ResponsePromptSingleChoice from 'components/ResponsePromptSingleChoice';
import { GameResponse, GameResponseOption, Profile } from 'types/configs';

interface Props {
  from: Profile;

  response: GameResponse;
  handleResponse: (response: GameResponseOption | GameResponseOption[]) => void;
  moduleClasses: (classes: string) => string;
}

const ResponsePrompt = ({ from, response, handleResponse, moduleClasses }: Props) =>
  response.multiChoice === true ? (
    <ResponsePromptMultipleChoice
      from={from}
      response={response}
      handleResponse={handleResponse}
      moduleClasses={moduleClasses}
    />
  ) : (
    <ResponsePromptSingleChoice
      from={from}
      response={response}
      handleResponse={handleResponse}
      moduleClasses={moduleClasses}
    />
  );

export default ResponsePrompt;
